	module.exports = {"data":{"markdownRemark":{"html":"<p>[[[nicovideo: videoid = sm1592729, alt = try beef pepper rice at home]]]</p>\n<p>Even if such a case happened, it does not change what I love about the signboard menu over there! I tried making it myself so that you can eat 3 meals a day every day anytime and anywhere.</p>\n<p>It was surprisingly nice to spend it on tequilo! Although it is good, if it says whether it is exactly the same as the real thing, it is still slightly different taste. Well how many times should I make this and adjust it?</p>\n<h2>material</h2>\n<ul>\n<li>\n<p>Cooked white rice bowl 1</p>\n</li>\n<li>\n<p>Beef (for curry) One hand mountain Saka degree</p>\n</li>\n<li>\n<p>Corn oyster mountain Saka 2 degree</p>\n</li>\n<li>\n<p>One blue onion (carved in advance)</p>\n</li>\n<li>\n<p>2 tablespoons butter</p>\n</li>\n<li>\n<p>1 tablespoon grated garlic</p>\n</li>\n<li>\n<p>1 Tbsp Tonkatsu Sauce</p>\n</li>\n<li>\n<p>Rubber pepper</p>\n</li>\n<li>\n<p>salt</p>\n<h2>recipe</h2>\n</li>\n</ul>\n<ol>\n<li>Put a cone in a tea bowl, put a spoonful of butter 1 on top of it, then grated with grated garlic, with sauce repeatedly</li>\n<li>Place the rest of the butter on the iron plate on the high heat (from here quickly!)</li>\n<li>Carefully rice is raised to the cup for about 9 minutes so as not to destroy the contents in the previous tea bowl (image that pushes the feeling more than plump)</li>\n<li>Butter melts and turns into a fox color and when smoke begins to emerge medium heat</li>\n<li>Turn the rice served in the bowl over the iron plate</li>\n<li>I will put the meat on the iron plate to surround the rice</li>\n<li>Hang shredded green onion from the top of the rice</li>\n<li>Sprinkle salt pepper evenly (with a lot of pleasure)</li>\n<li>Take it from the fire and bring it to the table with the iron plate as it is</li>\n<li>Stop stirring till the sound stops eating</li>\n</ol>","frontmatter":{"cover":null,"date":"2007-11-19T00:00:00+09:00","strDate":"2007/11/18","title":"I tried making beef pepper rice at home"}}},"pathContext":{"path":"/en/blog/2007/11/19-ビーフペッパーライスを自宅で作ってみた/","langKey":"en"}}

